export default {
	data() {
		return {
			classify: '',
			currentIndex: 0,
			scrollHeight: 0,
			loadingText: '上拉加载更多',
			pagination: 1,
			work: [],
			clicklistIndex: 1,
			clicklistIndex2: 3,
			showanimation: false
		}
	},
	onLoad() {
		var _this = this
		uni.getSystemInfo({
			success(res) {
				_this.scrollHeight = res.windowHeight
			}
		})
		uni.request({
			url: "https://m.jianzhimao.com/ajax/tasks?province=&city=%E5%B9%BF%E5%B7%9E&type=&category_1=&order=4&page=" +
				this.pagination + "&pageSize=20&channel_id=&from=",
			method: "GET",
			dataType: "json",
			success: (res) => {
				this.work = res.data.data
			}
		})
	},
	methods: {
		lower() {
			this.pagination = this.pagination + 1
			this.loadingText = '加载中'
			this.showanimation = true
			uni.request({
				url: "https://m.jianzhimao.com/ajax/tasks?province=&city=%E5%B9%BF%E5%B7%9E&type=&category_1=&order=4&page=" +
					this.pagination + "&pageSize=20&channel_id=&from=",
				method: "GET",
				dataType: "json",
				success: (res) => {
					if (res.data.data.length <= 0) {
						this.loadingText = '我是有底线的'
						this.showanimation = false
						return false
					}
					this.work = this.work.concat(res.data.data),
						this.loadingText = '上拉加载更多'
					this.showanimation = false
				}
			})
		},
		listdetail(e) {
			uni.navigateTo({
				url: '../detail/detail?id=' + e,
				success: res => {},
				fail: () => {},
				complete: () => {}
			})
		},
		click1(index) {
			if (this.currentIndex == 0 || this.currentIndex == 2) {
				this.currentIndex = index
			} else {
				this.currentIndex = 0
			}
		},
		click2(index) {
			if (this.currentIndex == 0 || this.currentIndex == 1) {
				this.currentIndex = index
			} else {
				this.currentIndex = 0
			}
		},
		clicklist(index) {
			this.clicklistIndex = index
			this.currentIndex = 0
		},
		clicklist2(index) {
			this.clicklistIndex2 = index
			this.currentIndex = 0
		}
	}
}
